<template>
  <table class="table table-hover align-middle">
    <thead class="table-dark">
      <tr>
        <th width="10" class="rounded-start">#</th>
        <th width="40%">Tên đợt thanh toán</th>
        <th class="text-center">Ngày lập</th>
        <th class="text-center">Trạng thái</th>
        <th>Tổng tiền</th>
        <th></th>
        <th class="rounded-end"></th>
      </tr>
    </thead>
    <tbody>
      <template v-for="(item, index) in commissions">
        <tr :key="index">
          <td>
            <button
              class="btn btn-default btn-xs"
              data-bs-toggle="collapse"
              :href="'#expand' + item.tunr_id"
              title="Xem chi tiết dịch vụ"
            >
              <i class="fa-regular fa-circle-plus"></i>
            </button>
          </td>
          <td>
            <strong class="text-primary">{{ item.turn_name }}</strong>
          </td>
          <td class="text-center">{{ item.turn_date | moment("DD/MM/YYYY") }}</td>
          <td class="text-center">
            <strong class="text-primary">{{ getStatus(item.turn_status) }}</strong>
          </td>
          <td></td>
          <td></td>
          <td>
            <div class="dropdown">
              <button
                class="btn border-0 btn-sm btn-round"
                type="button"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                <i class="fa-solid fa-ellipsis-vertical"></i>
              </button>
              <ul class="dropdown-menu">
                <!-- <li>
                                <a class="dropdown-item" href="#"><i class="fa-thin fa-trash-can"></i> Hủy phiếu</a>
                            </li> -->
                <li>
                  <span
                    class="hand dropdown-item"
                    @click="showModalComplete(item, index)"
                  >
                    <i class="fa-thin fa-circle-check"></i> Hoàn thiện
                  </span>
                </li>
              </ul>
            </div>
          </td>
        </tr>
        <tr v-for="(detail, index) in item.comissions" :key="detail.detail_id">
          <td></td>
          <td>
            <ul class="list-group">
              <li class="list-group-item bg-success bg-opacity-10">
                <strong>{{ detail.partner.partner_name }}</strong>
              </li>
              <li
                class="list-group-item"
                v-for="item in detail.details"
                :key="item.detail_id"
              >
                <router-link
                  tag="a"
                  :title="item.company_name"
                  class="text-dark fw-normal"
                  :to="{
                    name: 'commission-edit',
                    params: { id: item.comis_id },
                  }"
                >
                  {{ item.company_name }}
                </router-link>
              </li>
            </ul>
          </td>

          <td class="text-center">
            <ul class="list-group">
              <li class="list-group-item bg-light"><strong>Chi tiết</strong></li>
              <li
                class="list-group-item"
                v-for="item in detail.details"
                :key="item.company_name + item.comis_id"
              >
                <a class="text-danger">{{ formatAmout(item.comis_price) }}₫</a>
              </li>
            </ul>
          </td>
          <td class="text-center">
            {{ getStatus(detail.comis_status) }}
          </td>
          <td>
            <strong class="text-danger"> {{ formatAmout(detail.comis_total) }}đ </strong>
          </td>
          <td>
            <button
              title="Tải về file Đề nghị thu mua"
              @click="downloadFile(detail, 2)"
              class="btn btn-primary btn-sm me-2"
            >
              <i class="fa-solid fa-file-word"></i>
            </button>
            <button
              title="Tải về file đề nghị thanh toán"
              @click="downloadFile(detail, 1)"
              class="btn btn-primary btn-sm me-2"
            >
              <i class="fa-solid fa-file-word"></i>
            </button>
            <button
              title="Tải về file Bảng kê tổng hợp"
              class="btn btn-success btn-sm"
              @click="downloadFile(detail, 0)"
            >
              <i class="fa-solid fa-file-excel"></i>
            </button>
          </td>
          <td>
            <div class="dropdown">
              <button
                class="btn border-0 btn-sm btn-round"
                type="button"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                <i class="fa-solid fa-ellipsis-vertical"></i>
              </button>
              <ul class="dropdown-menu">
                <li>
                  <router-link
                    class="dropdown-item"
                    :to="{
                      name: 'commission-edit',
                      params: { id: detail.comis_id },
                    }"
                  >
                    <i class="fa-thin fa-pen-to-square"></i> Cập nhật phiếu
                  </router-link>
                </li>
                <li>
                  <a @click="updatePayment(detail)" class="dropdown-item hand"
                    ><i class="fa-thin fa-circle-dollar"></i> Đã nhận được tiền</a
                  >
                </li>
                <li>
                  <a
                    class="dropdown-item hand text-danger fw-bold"
                    @click="destroy(detail, index)"
                    ><i class="fa-thin fa-trash-can"></i> Hủy phiếu</a
                  >
                </li>
              </ul>
            </div>
          </td>
        </tr>
      </template>
    </tbody>
  </table>
</template>

<script>
import axios from "axios";
export default {
  props: {
    commissions: Array,
  },
  data() {
    return {
      types: [
        {
          type: 0,
          name: "Bang-Tong-Hop.xlsx",
        },
        {
          type: 1,
          name: "De-Nghi-Thanh-Toan.docx",
        },
        {
          type: 2,
          name: "De-Nghi-Thu-Mua.docx",
        },
      ],
    };
  },
  methods: {
    showModalComplete(item, index) {
      this.$emit("showModalComplete", item, index);
    },
    async updatePayment(item) {
      this.$confirm({
        title: "Cập phiếu thanh toán",
        message: "Bạn có thanh toán phiếu này không?",
        button: {
          yes: "Yes",
          no: "No",
        },
        callback: (confirm) => {
          if (confirm) {
            let seft = this;
            axios
              .put(`${API_URL}/commissions/update`, {
                comis_id: item.comis_id,
                comis_status: 2,
              })
              .then((response) => {
                if (response.status == 200) {
                  let commission = seft.commissions.filter((x) => {
                    return x.turn_id == item.turn_id;
                  })[0].comissions;

                  commission.filter((x) => {
                    return x.comis_id == item.comis_id;
                  })[0].comis_status = 2;
                  this.$toast.success("Cập nhật phiếu thanh toán thành công");
                }
              });
          }
        },
      });
    },
    async destroy(item, index) {
      let seft = this;
      this.$confirm({
        title: "Hủy phiếu thanh toán",
        message: "Bạn có muốn phiếu này không?",
        button: {
          yes: "Yes",
          no: "No",
        },
        callback: (confirm) => {
          if (confirm) {
            axios.delete(`${API_URL}/commissions/${item.comis_id}`).then((response) => {
              if (response.data == 200) {
                seft.commissions = seft.commissions.map((x) => {
                  x.comissions = x.comissions.filter((y) => y.comis_id !== item.comis_id);
                  return x;
                });
                seft.$toast.success("Xóa phiếu thành công");
              }
            });
          }
        },
      });
    },
    async downloadFile(detail, type) {
      this.$econtract.showLoading();
      await axios
        .post(
          `${API_URL}/commissions/export`,
          {
            comis_id: detail.comis_id,
            type: type,
          },
          {
            responseType: "blob",
          }
        )
        .then((response) => {
          let fileName = `${detail.partner.partner_name}-${this.types[type].name}`;
          let downloadLink = document.createElement("a");
          downloadLink.href = window.URL.createObjectURL(new Blob([response.data]));
          downloadLink.setAttribute("download", fileName);
          document.body.appendChild(downloadLink);
          downloadLink.click();
          this.$econtract.hideLoading();
        });
    },
    getStatus(status) {
      let _status = parseInt(status);
      switch (_status) {
        case 0:
          return "Mới tạo lập";
        case 1:
          return "Trình ký";
        case 2:
          return "Đã nhận được tiền";
      }
    },
    formatAmout(amount) {
      return this.$econtract.formatAmout(parseFloat(amount));
    },
  },
};
</script>
