<template>
  <b-modal
    v-show="isShow"
    ref="modalsearchturn"
    id="modalsearchturn"
    title="Danh sách phiếu thanh toán"
    no-close-on-backdrop
    no-fade
    size="xl"
    hide-footer
  >
    <div class="row mb-2">
      <div class="col-sm-10">
        <div class="input-group">
          <input
            type="text"
            placeholder="Nhập mã số thuế hoặc tên doanh nghiệp để tìm kiếm...."
            class="form-control"
            v-model="keyword"
            @keyup.enter="search()"
          />
        </div>
      </div>
      <div class="col-auto">
        <button
          type="button"
          id="button-addon2"
          class="btn btn-primary-soft"
          @click="search"
        >
          <i class="fa-solid fa-magnifying-glass"></i> Tìm kiếm
        </button>
      </div>
    </div>

    <table class="table">
      <thead>
        <tr>
          <th>#</th>
          <th width="30%">Tên công ty</th>
          <th>Mã số thuế</th>
          <th>Đợt thanh toán</th>
          <th>Ngày tạo</th>
          <th>Trạng thái</th>
          <th>Tiền hoa hồng</th>
          <th width="10"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item,index) in details" :key="item.detail_id">
          <td>{{ index+1 }}</td>
          <td>
            <strong class="text-primary">{{ item.company_name }}</strong>
          </td>
          <td>{{ item.company_taxcode }}</td>
          <td>{{ item.turn_name }}</td>
          <td>{{ item.turn_date | moment("DD/MM/YYYY") }}</td>
          <td>{{ getStatus(item.turn_status) }}</td>
          <td>
            <strong class="text-danger">{{ item.comis_price | formatamout }}đ</strong>
          </td>
          <td>
            <router-link
              :title="'Đến phiếu thanh toán '+item.turn_name"
              class="btn btn-primary-soft btn-sm"
              :to="{
                name: 'commission-edit',
                params: { id: item.comis_id },
              }"
            >
            <i class="fa-solid fa-circle-arrow-right"></i>
            </router-link>
          </td>
        </tr>
      </tbody>
    </table>
    <NotFound v-if="details.length == 0"></NotFound>
  </b-modal>
</template>

<script>
import axios from "axios";
import NotFound from "./NotFound.vue";
export default {
  name: "ModalSearchTun",
  components: {
    NotFound,
  },
  data() {
    return {
      isShow: false,
      details: [],
      keyword: "",
    };
  },
  methods: {
    show(keyword = null) {
      this.isShow = true;
      this.keyword = keyword;
      this.$bvModal.show("modalsearchturn");
      this.getDetails();
    },
    async search() {
      this.getDetails();
    },
    async getDetails() {
      await axios
        .post("/commissionsdetail/search", {
          name: this.keyword,
        })
        .then((response) => {
          this.details = response.data.data;
        });
    },
    getStatus(status) {
      let _status = parseInt(status);
      switch (_status) {
        case 0:
          return "Mới tạo lập";
        case 1:
          return "Trình ký";
        case 2:
          return "Đã nhận được tiền";
      }
    },
  },
  created() {
    // this.getDetails();
  },
};
</script>
