<template>
<b-modal v-if="isShow" ref="modalCompleteCommission" id="modalCompleteCommission" title="Hoàn thiện phiếu" no-close-on-backdrop size="md">
    <div class="row">
        <div class="col-12">

            <div class="form-group">
                <label for="">Tên đợt thanh toán</label>
                <input type="text" v-model="Turn.turn_name" disabled class="form-control">
            </div>

            <div class="form-group">
                <label for="">Trạng thái</label>
                <select class="form-select" v-model="Turn.turn_status">
                    <option value="0">Mới tạo lập</option>
                    <option value="1">Trình ký</option>
                    <option value="2">Đã nhận được tiền</option>
                </select>
            </div>
        </div>
    </div>
    <template #modal-footer>
      <button class="btn btn-primary-soft" @click="saveTurn">
        <i class="fa-thin fa-floppy-disk"></i> Lưu
      </button>
      <button class="btn btn-secondary" @click="$bvModal.hide('modalCompleteCommission')">
        <i class="fa-thin fa-xmark"></i> Đóng
      </button>
    </template>
</b-modal>
</template>

<script>
import axios from 'axios';
import moment from 'moment';
export default {
    data() {
        return {
            isShow: true,
            Turn: [],
            index:0,
        }
    },
    methods: {
        show(item) {
            this.Turn = item;
            this.$bvModal.show('modalCompleteCommission');
        },
        async saveTurn (){
            this.Turn.turn_date_payment = moment();
            console.log( this.Turn.turn_date_payment )
            await axios.put(`/turns/update`,this.Turn).then(response=>{
                if(response.status == 200){
                    this.$emit('updateCommission',response.data);
                    this.$bvModal.hide('modalCompleteCommission');
                    this.$toast.success('Cập nhật phiếu thanh toán thành công');
                }
            })
        },
    }
}
</script>
